// mobile detect
(function($){
    window.cthMobileDetect = function(){
        var a = {
            Android: function () {
                return navigator.userAgent.match(/Android/i);
            },
            BlackBerry: function () {
                return navigator.userAgent.match(/BlackBerry/i);
            },
            iOS: function () {
                return navigator.userAgent.match(/iPhone|iPad|iPod/i);
            },
            Opera: function () {
                return navigator.userAgent.match(/Opera Mini/i);
            },
            Windows: function () {
                return navigator.userAgent.match(/IEMobile/i);
            },
            any: function () {
                return a.Android() || a.BlackBerry() || a.iOS() || a.Opera() || a.Windows();
            }
        };
        var trueMobile = a.any();
        if(null == trueMobile){
            $('body').removeClass('cth-mobile');
            return false;
        }else{
            $('body').addClass('cth-mobile');
            return true;
        }
    }
    $(function(){
        cthMobileDetect();
        $(window).on('resize',function(){cthMobileDetect()})

        //   scroll to------------------
        $(".cth-scroll-link").on("click", function () {
            var a = 10+ $(".main-header").height();
            if (location.pathname.replace(/^\//, "") == this.pathname.replace(/^\//, "") || location.hostname == this.hostname) {
                var b = $(this.hash);
                b = b.length ? b : $("[name=" + this.hash.slice(1) + "]");
                if (b.length) {
                    $("html,body").animate({
                        scrollTop: b.offset().top - a
                    }, {
                        queue: false,
                        duration: 1200,
                        easing: "easeInOutExpo"
                    });
                    return false;
                }
            }
        });


    });

})(jQuery);
    


(function($){
    'use strict';
    var tdOpt = {
        setCurrentTime: false,
        meridians: true, // false for 24h
        format: 'h:mm a', // HH:mm for 24h - h:mm a
        primaryColor: "#4DB7FE",
        borderColor: "#4DB7FE",
        minutesInterval: '15'
    };
    if(_townhub_add_ons.lb_24h){
        tdOpt.meridians = false;
        tdOpt.format = 'HH:mm';
    }
    if(_townhub_add_ons.td_color != ''){
        tdOpt.primaryColor = tdOpt.borderColor = _townhub_add_ons.td_color;
    }
    $('input.datepicker').each(function(){
        $(this).dateDropper();
    })
    $("input.timepicker").each(function(){
        $(this).timeDropper(tdOpt);
    })
    $(".eye").on("click touchstart", function () {
        var epi = $(this).parent(".pass-input-wrap").find("input");
        if (epi.attr("type") === "password") {
            epi.attr("type", "text");
        } else {
            epi.attr("type", "password");
        }
    });
    $(function(){
        $(document).on('click', '.remove-date-time', function(e){
            e.preventDefault();
            $(this).parent().children('input').val('').trigger('change');
        })
    });
    


})(jQuery);

(function($){
	'use strict';
	$(document).ready(function() {
		$('.townhub-tweet').each(function(){
	        var that = $(this);
	        var username = that.data('username'),
	            list = that.data('list'),
	            hashtag = that.data('hashtag'),
	            ticker = that.data('ticker'),
	            count = that.data('count');
	        that.twittie({
	            username: username,
	            list: list,
	            hashtag: hashtag,
	            dateFormat: '%b %d, %Y',
	            template: 	'<div class="user">'+
								'<div>'+
								  	'<a href="https://twitter.com/{{user_name}}" target="_blank">'+
								    	'<span>{{avatar}}</span>'+
								  	'</a>{{screen_name}}'+
								'</div>'+
							'</div>'+
							'<p class="tweet">{{tweet}}</p>'+
							'<p class="timePosted"><a href="{{url}}">'+_townhub_add_ons.posted_on+'{{date}}</a></p>'+
							'<p class="interact">'+
								'<a href="https://twitter.com/intent/tweet?in_reply_to={{id_str}}" class="twitter_reply_icon" target="_blank">'+_townhub_add_ons.reply+'</a>'+
								'<a href="https://twitter.com/intent/retweet?tweet_id={{id_str}}" class="twitter_retweet_icon" target="_blank">'+_townhub_add_ons.retweet+'</a>'+
								'<a href="https://twitter.com/intent/favorite?tweet_id={{id_str}}" class="twitter_fav_icon" target="_blank">'+_townhub_add_ons.favorite+'</a>'+
							'</p>',


	            count: count,
	            loadingText: 'Loading!',
	            apiPath: _townhub_add_ons.url,
	            action: 'townhub_get_tweets',
	            hideReplies: false
	        },function () {
	            if(ticker == 'yes'){
	                $('ul',that).list_ticker({
	                    speed:5000,
	                    effect:'fade'
	                });
	            }
	        });



	    });

		$('.townhub_mailchimp-form').each(function(){
			var sub_form = $(this);
			sub_form.submit(function(e){
				e.preventDefault();
				sub_form.find('.subscribe-message').fadeIn('slow').text(_townhub_add_ons.pl_w);
				var dataString = sub_form.serialize();
		            dataString += '&action=townhub_mailchimp';
		        var noredirect = true;
				$.ajax({
		            type: "POST",
		            data: dataString,
		            url: _townhub_add_ons.url,
		            success: function(d) {
		                if(d.success){
		                	if (d.success === 'yes') {
			                    if (noredirect) {
			                    	sub_form.find('.subscribe-message').fadeIn('slow').text(d.message);//.delay(3000).fadeOut('slow');
			                    } 
			                    else {
			                        window.location.href = redirect;
			                    }
			                } else {
                                sub_form.find('.subscribe-message').fadeIn('slow').text(d.message);//.delay(3000).fadeOut('slow');
                                if(typeof d.last_response.body != undefined){
                                    var bodyjson = JSON.parse(d.last_response.body);
                                    sub_form.find('.subscribe-message').fadeIn('slow').text(bodyjson.title);//.delay(3000).fadeOut('slow');
                                }
			                	
			                }
		                }
		            }
		        });
                // end $.ajax

			});
		});

	});

})(jQuery);

