    $(function(){
        /*init instagram feed */
        if(jQuery('.townhub-instafeed').length){
            jQuery('.townhub-instafeed').each(function(){
                var clientID = jQuery(this).data('client');
                var accessToken = jQuery(this).data('access');
                var limit = jQuery(this).data('limit');
                var get = jQuery(this).data('get');
                var getval = jQuery(this).data('getval');
                var res = jQuery(this).data('res');
                var target = jQuery(this).find('.townhub-instafeed-ul').attr('id');
                
                var instafeed_ops = {
                        get: get,
                        target: target,
                        resolution: res,// thumbnail | low_resolution 
                        template: '<li><a class="insta-popup" href="{{link}}"><img src="{{image}}" alt="{{caption}}"/></a></li>',
                        limit: limit,
                        error: function(err){
                            console.log(err);
                        }
                        
                } ;
                // for popup option
                instafeed_ops.template = '<li><a class="insta-popup" href="{{image}}"><img src="{{image}}" alt="{{caption}}"/></a></li>';
                instafeed_ops.after = function(){
                    $('#'+target).lightGallery({
                        selector: ".insta-popup",
                        cssEasing: "cubic-bezier(0.25, 0, 0.25, 1)",
                        download: false,
                        loop: false,
                        counter: false,
                    });
                };


                if(clientID != '') {
                    instafeed_ops.clientID = clientID;
                }
                if(accessToken != '') {
                    instafeed_ops.accessToken = accessToken; 
                }
                if(get == 'tagged'){
                    instafeed_ops.tagName = getval;
                }else if(get == 'user'){
                    instafeed_ops.userId = getval;
                }
                var feed = new Instafeed(instafeed_ops);
                feed.run();
            });
        }
    });

