	    var appOSM = {},
	    	inherits = ol.inherits,
	    	defaultInteractions = ol.interaction.defaults,
	    	PointerInteraction = ol.interaction.Pointer;
		/**
		* @constructor
		* @extends {module:ol/interaction/Pointer}
		*/
		appOSM.Drag = function() {

			PointerInteraction.call(this, {
				handleDownEvent: appOSM.Drag.prototype.handleDownEvent,
				handleDragEvent: appOSM.Drag.prototype.handleDragEvent,
				handleMoveEvent: appOSM.Drag.prototype.handleMoveEvent,
				handleUpEvent: appOSM.Drag.prototype.handleUpEvent
			});

			/**
			 * @type {module:ol/pixel~Pixel}
			 * @private
			 */
			this.coordinate_ = null;

			/**
			 * @type {string|undefined}
			 * @private
			 */
			this.cursor_ = 'pointer';

			/**
			 * @type {module:ol/Feature~Feature}
			 * @private
			 */
			this.feature_ = null;

			/**
			 * @type {string|undefined}
			 * @private
			 */
			this.previousCursor_ = undefined;

		};

		inherits(appOSM.Drag, PointerInteraction); 

		/**
		* @param {module:ol/MapBrowserEvent~MapBrowserEvent} evt Map browser event.
		* @return {boolean} `true` to start the drag sequence.
		*/
		appOSM.Drag.prototype.handleDownEvent = function(evt) {
			let map = evt.map;

			let feature = map.forEachFeatureAtPixel(evt.pixel, function(feature) {
					return feature;
			});
			// console.log(feature)
			if (feature) {
				this.coordinate_ = evt.coordinate;
				this.feature_ = feature;
			}

			// console.log(this.coordinate_)

			return !!feature;
		};


		/**
		* @param {module:ol/MapBrowserEvent~MapBrowserEvent} evt Map browser event.
		*/
		appOSM.Drag.prototype.handleDragEvent = function(evt) {
			var deltaX = evt.coordinate[0] - this.coordinate_[0];
			var deltaY = evt.coordinate[1] - this.coordinate_[1];

			var geometry = this.feature_.getGeometry();
			geometry.translate(deltaX, deltaY);
			// geometry: new ol.geom.Point(ol.proj.transform(mapCenter, 'EPSG:4326', 'EPSG:3857')),
			this.coordinate_[0] = evt.coordinate[0];
			this.coordinate_[1] = evt.coordinate[1];

			// console.log(this.coordinate_)

			if(this.coordinate_ != null) onOSMDragMarker(ol.proj.transform(this.coordinate_, 'EPSG:3857', 'EPSG:4326'))
		};


		/**
		* @param {module:ol/MapBrowserEvent~MapBrowserEvent} evt Event.
		*/
		appOSM.Drag.prototype.handleMoveEvent = function(evt) {
			if (this.cursor_) {
				var map = evt.map;
				var feature = map.forEachFeatureAtPixel(evt.pixel,function(feature) {
				  	return feature;
				});	
				var element = evt.map.getTargetElement();
				if (feature) {
					if (element.style.cursor != this.cursor_) {
					  this.previousCursor_ = element.style.cursor;
					  element.style.cursor = this.cursor_;
					}
				} else if (this.previousCursor_ !== undefined) {
					element.style.cursor = this.previousCursor_;
					this.previousCursor_ = undefined;
				}
			}
			// console.log(this.coordinate_)
		};


