    // Attaches an info window to a marker with the provided listing content. When the
    // marker is clicked, the info window will open with the content.
    function attachMarkerInfoContent(marker, infoContent) {
        var theMap = marker.get('map');
        // boxText.innerHTML = infoContent;
        // var ib = new InfoBox(boxOptions);
        marker.addListener('click', function() {
            window.currentMarker = marker.id

            boxText.innerHTML = infoContent;
            var ib = new InfoBox(boxOptions);
            
            ib.open(theMap, marker);

            

            // pan to marker
            theMap.panTo(marker.getPosition());
            theMap.panBy(0, -180);
            // close infoBox
            google.maps.event.addListener(ib, 'domready', function () {
                cardRaining();
                $('.infoBox-close').click(function (e) {
                    e.preventDefault();
                    ib.close();
                });
            });
            theMap.addListener('click', function(e) {
                ib.close();
            });
            theMap.addListener('zoom_changed', function(e) {
                ib.close();
            });
            
            
        });
        // animate marker when hover listing item
        // marker.addListener('mouseover', function() {
        //     marker.setAnimation(google.maps.Animation.BOUNCE);
        // });
        // marker.addListener('mouseout', function() {
        //     marker.setAnimation(null);
        // });
        
    }

    function ZoomControl(zoomControlDiv, theMap) {
        zoomControlDiv.index = 1;
        zoomControlDiv.className = "zoom-controls-wrap";
        // https://developers.google.com/maps/documentation/javascript/controls#ControlPositioning

        if(cthMobileDetect()){
            theMap.controls[google.maps.ControlPosition.BOTTOM_LEFT].push(zoomControlDiv);
            zoomControlDiv.classList.add("for-mobile");
        }else{
            theMap.controls[google.maps.ControlPosition.RIGHT_CENTER].push(zoomControlDiv);
        }
        
        zoomControlDiv.style.padding = '5px';
        var controlWrapper = document.createElement('div');
        
        zoomControlDiv.appendChild(controlWrapper);
        var zoomInButton = document.createElement('div');
        zoomInButton.className = "mapzoom-in";
        controlWrapper.appendChild(zoomInButton);
        var zoomOutButton = document.createElement('div');
        zoomOutButton.className = "mapzoom-out";
        controlWrapper.appendChild(zoomOutButton);
        google.maps.event.addDomListener(zoomInButton, 'click', function () {
            theMap.setZoom(theMap.getZoom() + 1);
        });
        google.maps.event.addDomListener(zoomOutButton, 'click', function () {
            theMap.setZoom(theMap.getZoom() - 1);
        });
    }
    

    // ajax helpers
    var AjaxHelper = {
        config: {},

        initConfig: function() {
            this.config = {
                ajaxParams: {
                    type: 'POST',
                    url: _townhub_add_ons.url,
                    data: {}
                },
                actionPrefix: 'townhub_addons_'
            };
        },

        init: function() {
            this.initConfig();
        },

        send: function( action, options ) {
            var ajaxParams = JSON.parse(JSON.stringify(this.config.ajaxParams));

            options = options || {};

            action = this.config.actionPrefix + action;

            $.extend( ajaxParams, options );

           


            if ( ajaxParams.data instanceof FormData ) {
                
                ajaxParams.data.append( 'action', action );
                ajaxParams.data.append( '_nonce', _townhub_add_ons.nonce );
            } else {
                
                ajaxParams.data.action = action;
                ajaxParams.data._nonce = _townhub_add_ons.nonce;
            }

            var successCallback = ajaxParams.success,
                errorCallback = ajaxParams.error;

            if ( successCallback || errorCallback ) {
                ajaxParams.success = function( response ) {
                    
                    if ( response.success && successCallback ) {
                        successCallback( response.data );
                    }

                    if ( ( ! response.success ) && errorCallback ) {
                        
                        errorCallback( response.data );
                    }
                };

                if ( errorCallback ) {
                    ajaxParams.error = function( data ) {
                        
                        errorCallback( data );
                    };
                }
            }

            return $.ajax( ajaxParams );
        }
    };

    AjaxHelper.init();




