    // https://stackoverflow.com/questions/3144419/how-do-i-remove-a-file-from-the-filelist
    // https://www.w3.org/TR/FileAPI/#dfn-filelist
    // https://html.spec.whatwg.org/multipage/input.html#common-input-element-apis
    // https://developer.mozilla.org/en-US/docs/Web/API/File/Using_files_from_web_applications
    $(document).on('change','.fuzone input[type="file"]',function(e){
        var input = this;
       
        $(input).closest('.fuzone').children('.fu-imgs').html('')
        // var inputFiles = input.files
        if(input.files.length > _townhub_add_ons.md_limit){
            alert(_townhub_add_ons.md_limit_msg);
            input.value='';
        }else if(input.files.length > 0) {
            _.each(input.files,function(file,index){
                if(file.type != undefined && file.type.indexOf('image/') === 0){
                    if(file.size/1024/1024 > _townhub_add_ons.md_limit_size){
                        alert(_townhub_add_ons.md_limit_size_msg);
                        input.value='';
                    }else{
                        var reader = new FileReader();
                        reader.onload = function (e) {
                            $(input).closest('.fuzone').children('.fu-imgs').append('<img src="'+e.target.result+'">');
                        }
                        reader.readAsDataURL(file);
                    }
                }else{
                    

                    input.value='';
                }
                    
            });
        }
        
    });
