    // get form data object
    function getFormDataObj(dataArray){
        
        var paramObj = {};
        if(dataArray){
            $.each(dataArray, function(_, kv) {
                if (paramObj.hasOwnProperty(kv.name)) {
                    paramObj[kv.name] = $.makeArray(paramObj[kv.name]);
                    paramObj[kv.name].push(kv.value);
                }else {
                    paramObj[kv.name] = kv.value;
                }
            });
        }
        
        return paramObj
    }

    var ajax_count = 1;

    // 2 - do ajax search listings with form data
    /*
    * form - filter form element
    * vars - object - additional filter variables
    */
    function doAjaxSearch(form, vars, bounds_changed){
        ajax_count++;
        var data = getFormDataObj($(form).serializeArray());
        if(vars != null) $.extend(true, data, vars)
        data['ajax_count'] = ajax_count
        if(!$('.listings-loader').length) $('#listing-items').addClass('loading-listings').after('<div class="listings-loader"><i class="fa fa-spinner fa-pulse fa-3x"></i></div>')
        AjaxHelper.send('ajax_search', {
            data : data,
            success: function(res){
            	
                if(res.ajax_count != undefined && res.ajax_count == ajax_count){
                    
                    var template = _.template( $('#tmpl-no-results').html(), {variable: 'data', evaluate: /<#([\s\S]+?)#>/g, interpolate: /{{{([\s\S]+?)}}}/g, escape: /{{([^}]+?)}}(?!})/g,});
                	var listingTypeTmpls = {};

                    $('#listing-items').html('');

                    if(res.posts && res.posts.length){
                        
                        _.each(_townhub_add_ons.listing_types, function(typeID){
                            listingTypeTmpls['listing_template_type_'+typeID] = _.template( $('#tmpl-listing-item-type-'+typeID).html(), {variable: 'data', evaluate: /<#([\s\S]+?)#>/g, interpolate: /{{{([\s\S]+?)}}}/g, escape: /{{([^}]+?)}}(?!})/g,})
                        
                            // console.log(listingTypeTmpls);
                        })
                        
                        _.each(res.posts, function(listing){
                        	if(listingTypeTmpls['listing_template_type_'+listing.typeID] != null)
                            	$('#listing-items').append( listingTypeTmpls['listing_template_type_'+listing.typeID]( listing ) );
                            else
                                $('#listing-items').append( listingTypeTmpls[Object.keys(listingTypeTmpls)[0]]( listing ) );
                        });
                    }else{
                        $('#listing-items').html( template( ) );
                    }
                      
                    // trigger listingsChanged event on window
                    onListingsChanged('ajax_search')
                    if ( document.getElementById('map-main') != null ) initMainMap( res.posts, bounds_changed )
                    if ( document.getElementById('map-osm-main') != null ) {
                    	// initMainOSMMap( res.posts, bounds_changed )
                    	mainOSMApp.updateMap(res.posts)
                    }
                    cardRainingVis()
                    // update pagination
                    if(typeof res.pagination != 'undefined') $('.listings-pagination-wrap').html(res.pagination)
                    $('#listing-items').removeClass('loading-listings')
                    $('.listings-loader').remove()
                }// end eheck ajax count
            },
            error: function(res){
                
            }
        });
    }
    // https://stackoverflow.com/questions/23344625/create-javascript-custom-event
    function onListingsChanged(state){
        // create new window event for listing change
        var evt = new CustomEvent('listingsChanged', { detail: state });
        
        window.dispatchEvent(evt);
    }

    // get current geo location
    function getPreciseLocation() {
        return new Promise(function (resolve, reject) {
            navigator.geolocation.getCurrentPosition(function (position) {
                resolve({
                    lat: position.coords.latitude,
                    lng: position.coords.longitude
                });
            });
        });
    }

    function getLocationDataResult(data){
        let location_result = ''
        if( data !== null && typeof data === 'object' ){
            if(Array.isArray(_townhub_add_ons.address_format) && _townhub_add_ons.address_format.length){
                let result_parts = _townhub_add_ons.address_format.filter((part)=>{return  data[part] != null && data[part] !=  '' }).map( (part)=>{return data[part]} );
                location_result = result_parts.join(', ');
            }else if(data[_townhub_add_ons.location_type] != null){
                location_result = data[_townhub_add_ons.location_type]
            }
        }
        return location_result
    }

    function extractAddressComponent(addresses, data){
        var location_result = '';
        $.each(addresses, function(i, address_component) {
            // and new formatted_address from google
            if(data['formatted_address'] == null && address_component['formatted_address']) data['formatted_address'] = address_component['formatted_address'];
            if(typeof data != 'undefined' ) data[address_component.types[0]] = address_component.address_components[0].long_name;
            if (address_component.types[0] == _townhub_add_ons.location_type) { // political or locality
                location_result = address_component.address_components[0].long_name;
            }
        });
        
        location_result = getLocationDataResult(data)
        if( data !== null && typeof data === 'object' ){
            data.result = location_result;
            return data;
        }
        return location_result;

    }

    function extractOSMAddress(address, data){
        let location_result = '';
        if(address.display_name != null) data['formatted_address'] = address.display_name
        if(address.address != null){
            if(address.address.country != null) data['country'] = address.address.country
            if(address.address.state != null) data['administrative_area_level_1'] = address.address.state
            if(address.address.city != null) 
                data['administrative_area_level_2'] = address.address.city
            else if(address.address.county != null)
                data['administrative_area_level_2'] = address.address.city

            if(address.address.postcode != null) data['postal_code'] = address.address.postcode
            if(address.address.village != null) data['locality'] = address.address.village
            if(address.address.city_district != null) data['sublocality'] = address.address.city_district

        }

        location_result = getLocationDataResult(data)

        if( data !== null && typeof data === 'object'){
            data.result = location_result;
            return data;
        }
        return location_result;
    }

    function addCurrentCity(geolocation) {
        if (_townhub_add_ons.free_map == '1'){
            return new Promise((resolve, reject) => {
                let api = `https://nominatim.openstreetmap.org/reverse?format=json&lat=${geolocation.lat}&lon=${geolocation.lng}&zoom=18&addressdetails=1`
                fetch(api)
                .then(function(response) {
                    // console.log(response)

                    return response.json();
                })
                .then(function(address) {
                    
                    geolocation = extractOSMAddress(address, geolocation);
                    resolve(geolocation);
                });
            });
        }else{
            var geocoder = new google.maps.Geocoder;
            return new Promise(function (resolve, reject) {
                geocoder.geocode({location: geolocation}, function(results, status) {
                    if (status === 'OK') {
                        if (results[0]) {
                            geolocation = extractAddressComponent(results, geolocation);
                            resolve(geolocation);
                        }
                    }
                });


            });
        }
            
    }

    function getCurrentGeoLocation(callback){

        var data = Cookies.get('cth-geolocation');
        if( data != null && data ){
            if( typeof data == 'string' ) data = JSON.parse(data);

            if(_.isArray(_townhub_add_ons.address_format) && _townhub_add_ons.address_format.length){
                var result_parts = _townhub_add_ons.address_format.filter((part)=>{return typeof data[part] != 'undefined' && data[part] !=  '' }).map( (part)=>{return data[part]} );
                data.result = result_parts.join(', ');
            }else if(typeof data[_townhub_add_ons.location_type] != 'undefined' && data.result != data[_townhub_add_ons.location_type] ){
                data.result = data[_townhub_add_ons.location_type]
            }
            callback(data)
        }else{
            getPreciseLocation()
                .then(addCurrentCity)
                .then((data)=>{
                    Cookies.set('cth-geolocation', data);
                    callback(data)
                })
        }
    }

