    //daterangepicker--------------
    function formatDate(date, modify, format = 'YYYY-MM-DD') {
        if(date == null || date == '') 
            date = new Date()
        else  
            date = new Date(date)
        if(typeof date.getMonth !== 'function') 
            date = new Date()
        let d = date.getDate()
        if(modify != null && !isNaN(parseInt(modify))){
            d += parseInt(modify)
            date.setDate(d)
        }
        
        d = date.getDate()
        let y = date.getFullYear(), m = date.getMonth(), fullm = _getMonthNum(m);
        let fulld = _getDateNum(d)
        if(format != '')
            // http://blog.stevenlevithan.com/archives/date-time-format
            return format.replace(/YYYY/g, y).replace(/MM/g, fullm).replace(/DD/g, fulld)
        else
            return date.toString()
    };

    function _getMonthNum(month){
        month = parseInt(month) + 1
        if(month < 10) return '0'+month
        return month
    }
    function _getDateNum(date){
        if(date < 10) return '0'+date
        return date
    }
    $('.listing-search-dates').each(function(){
            let ckinval = Cookies.get('esb_checkin'),
                ckoutval = Cookies.get('esb_checkout');
            ckinval = formatDate(ckinval, null, 'MM/DD/YYYY')
            // console.log(ckinval);
            if(ckoutval)
                ckoutval = formatDate(ckoutval, null, 'MM/DD/YYYY')
            else 
                ckoutval = formatDate(null, 1, 'MM/DD/YYYY')
            // console.log(ckoutval);
            $('.listing-search-dates').daterangepicker({
                autoUpdateInput: false,
                autoApply: true,
                parentEl: $(this).parent(),
                locale: {
                    cancelLabel: 'Clear'
                },
                startDate: ckinval, 
                endDate: ckoutval,
                minDate: formatDate(null, null, 'MM/DD/YYYY'), //'12/28/2018'
            });
            $('.listing-search-dates').on('apply.daterangepicker', function (ev, picker) {
                let checkin = picker.startDate.format('YYYY-MM-DD'),
                    checkout = picker.endDate.format('YYYY-MM-DD');

                $(this).val(checkin + ' -> ' + checkout);
                $('input[name="checkin"]').val(checkin);
                $('input[name="checkout"]').val(checkout);

                Cookies.set('esb_checkin', checkin, { expires: 7 });
                Cookies.set('esb_checkout', checkout, { expires: 7 });
            });
            $('.listing-search-dates').on('cancel.daterangepicker', function (ev, picker) {
                $(this).val('');
            });
        });
