(function($) {
    

    // https://codeburst.io/how-to-the-masonry-layout-56f0fe0b19df
    // Classname reference
    const CLASSES = {
        MASONRY: 'listing-items',
        PANEL  : 'listing-item',
        PAD    : 'masonry-pad',
    }

    const masonryConfig = {
        sm: {
            width: 430,
            columns: 1,
        },
        md: {
            width: 768,
            columns: 2,
        },
        // lg: {
        //     width: 992,
        //     columns: 3,
        // },
        // xl: {
        //     width: 1500,
        //     columns: 4,
        // },
        lg: {
            width: 992,
            columns: 3,
        },
        four: {
            columns: 4,
        },
        five: {
            columns: 5,
        },
        six: {
            columns: 6,
        },
        seven: {
            columns: 7,
        },
        eight: {
            columns: 8,
        },
        nine: {
            columns: 9,
        },
        ten: {
            columns: 10,
        },
    }

    class Masonry {
        constructor(el) {
            if(el === null) return false
            const {
                layout
            } = this
            this.container = el
            this.panels = el.querySelectorAll(`.${CLASSES.PANEL}`) 
            this.state = {
                heights: [],
            }
            this.layout()

            /**
             * To make responsive, onResize layout again
             * NOTE:: For better performance, please debounce this!
             */
            window.addEventListener('resize', _.debounce(()=>{this.layout()}, 100))
            window.addEventListener('listingsChanged', _.debounce(()=>{
                
                this.__reInit()
                // this.layout()
                imagesLoaded(el, (instance) => {
                    this.layout()
                })
            }, 100))
            const load = imagesLoaded(el, (instance) => {
                // _.debounce(layout.call(this), 500)
                this.layout()
            })
            // load.on('progress', (instance, image) => {
            //   // This trick allows us to avoid any floating pixel sizes 
            //   // image.img.style.height = image.img.height
            //   // image.img.setAttribute('height', image.img.height)
            //   image.img.classList.remove('loading')
            //   // NOTE: Not the cleanest thing to do here but this is a demo 
            //   // const parentPanel = image.img.parentNode.parentNode
            //   // parentPanel.setAttribute('style', `height: ${image.img.height}px`)
            //   // parentPanel.classList.remove(`${CLASSES.PANEL}--loading`)
            //   this.layout()
            // })
        }
        /**
          * Re init panels for adding new items
          *
        */
        __reInit(){
            const {
                container,
            } = this
            this.panels = container.querySelectorAll(`.${CLASSES.PANEL}`)
            // this.state = {
            //     heights: [],
            // } 
        }
        /**
          * Reset the layout by removing padding elements, resetting heights
          * reference and removing the container inline style
        */
        __reset() {
            const {
                container,
            } = this
            this.state.heights = []
            const fillers = container.querySelectorAll(`.${CLASSES.PAD}`)
            if (fillers.length) {
                for(let f = 0; f < fillers.length; f++) {
                    fillers[f].parentNode.removeChild(fillers[f])
                }
            }
            container.removeAttribute('style')
        }
        /**
          * Iterate through panels and work out the height of the layout
        */
        __populateHeights() {
            const {
                panels,
                state,
            } = this
            const {
                heights,
            } = state
            if(panels.length){
                for (let p = 0; p < panels.length; p++) {
                    const panel = panels[p]
                    const {
                        order: cssOrder,
                        msFlexOrder,
                        height,
                        marginTop,
                        marginBottom
                    } = getComputedStyle(panel)
                    // const style = panel.currentStyle || getComputedStyle(panel);
                    const order = cssOrder || msFlexOrder
                    if (!heights[order - 1]) heights[order - 1] = 0
                    heights[order - 1] = parseInt(heights[order - 1], 10) + parseInt(height, 10) + parseInt(marginTop, 10) + parseInt(marginBottom, 10)

                }
            }
            
        }
        /**
          * Set the layout height based on referencing the content cumulative height
          * This probably doesn't need its own function but felt right to be nice
          * and neat
        */
        __setLayout() {
            const {
                container,
                state,
            } = this
            const {
                heights,
            } = state
            this.state.maxHeight = Math.max(...heights)
            container.style.height = `${this.state.maxHeight+10}px`
        }
        // /**
        //   * JavaScript method for setting order of each panel based on panels.length and desired number of columns
        // */
        // __setOrders() {
        //     const {
        //         panels,
        //     } = this
        //     const cols = 2 // There needs to be an internal reference here that checks how many cols for viewport size
        //     panels.forEach((panel, idx) => {
        //         panel.style.order = ((idx + 1) % cols === 0) ? cols : (idx + 1) % cols
        //     })
        // }
        /**
          * Pad out layout "columns" with padding elements that make heights equal
        */
        __pad() {
            const {
                container,
            } = this
            const {
                heights,
                maxHeight,
            } = this.state
            // modify to add pad if there is not enough panel in a row
            const currentSize = this._getLayoutSize()
            if(heights.length < masonryConfig[currentSize].columns){
                const remainder = masonryConfig[currentSize].columns - heights.length
                for (let p = 0; p < remainder; p++){
                    heights.push(0)
                }
            }
            
            heights.forEach((height, idx) => {
                // if (height < maxHeight && height > 0) {
                if (height < maxHeight ) {
                    const pad             = document.createElement('div')
                    pad.className         = CLASSES.PAD
                    pad.style.height      = `${maxHeight - height}px`
                    pad.style.width      = `${100/masonryConfig[currentSize].columns}%`
                    pad.style.order       = idx + 1
                    pad.style.msFlexOrder = idx + 1
                    container.appendChild(pad)
                }
            })
        }
        /**
          * Resets and lays out elements
        */
        layout() {
            // this.__createNewPanels()

            this.__reset()
            // this.__setOrders()
            this.__populateHeights()
            this.__setLayout()
            this.__pad()

            // this.__createNewPanels()
        }

        // new for column larger than 2
        

        // return current layout size config to use
        _getLayoutSize(){
            const {
                container,
            } = this
            const n = window.innerWidth
            if(n< 768) return 'sm'
            if(n< 992) return 'md'
            // if(n< 1500) return 'lg'
            // return 'xl'
            // config for listing gallery wraper
            var manWrap = $(container).closest('.listings-grid-wrap')
            if(manWrap.hasClass('one-cols')) return 'sm'
            else if(manWrap.hasClass('two-cols')) return 'md'
            else if(manWrap.hasClass('four-cols')) return 'four'
            else if(manWrap.hasClass('five-cols')) return 'five'
            else if(manWrap.hasClass('six-cols')) return 'six'
            else if(manWrap.hasClass('seven-cols')) return 'seven'
            else if(manWrap.hasClass('eight-cols')) return 'eight'
            else if(manWrap.hasClass('nine-cols')) return 'nine'
            else if(manWrap.hasClass('ten-cols')) return 'ten'
            return 'lg'
        }

        // class method
        // __setOrders(){
        //     const {panels} = this
        //     const currentSize = this._getLayoutSize()
        //     panels.forEach((panel,idx)=>{
        //         let order = idx % masonryConfig[currentSize].columns
        //         if(order === 0) order = masonryConfig[currentSize].columns
        //         panel.style.order = order
        //     })
        // }

        /**
          * Create new hidden panel elements to push real panel in right order
        */
        __createNewPanels() {
            const {
                container,
                panels
            } = this
            if(panels.length){
                const currentSize = this._getLayoutSize()
                let remainder = panels.length % masonryConfig[currentSize].columns
                
                if(remainder > 0){
                    let remainder_panel = masonryConfig[currentSize].columns - remainder
                    
                    for (let p = 0; p < remainder_panel; p++){
                        const panel             = document.createElement('div')
                        panel.className         = CLASSES.PANEL
                        panel.className         += ' hidden-listing'
                        panel.style.height      = '1px'
                        container.appendChild(panel)
                    }
                }
            }
        }
    }

    window.listingsMasonry = new Masonry(document.querySelector(`.${CLASSES.MASONRY}`))
    /**
    * To make responsive, onResize layout again
    * NOTE:: For better performance, please debounce this!
    */
    // window.addEventListener('resize', () => myMasonry.layout())

    // $(function() {
    //     $('.listing-items').imagesLoaded( function() {
    //         // images have loaded
    //         $('.listing-item').matchHeight();
    //     });
        
    // });


})(jQuery);

